import React from 'react';
import './HeaderWallet.scss';
import { networkName } from 'config';

interface HeaderWalletProps {
  connect?: any;
  cureentAddress?: string;
  disconnect?: any;
  label?: string;
  isNetwork?: boolean;
}

function Wallet(props: HeaderWalletProps) {
  return (
    <div id="header_box_r_wallet">
      <input id="header_box_r_wallet_button" type="checkbox" />
      {/*props.cureentAddress && (
        <label htmlFor="header_box_r_wallet_button" onClick={props.disconnect}>
          Disconnect
        </label>
      )*/}
      <input id="header_box_r_wallet_button" type="checkbox" />
      <label htmlFor="header_box_r_wallet_button" onClick={props.connect}>
        {props.cureentAddress ? (
          <span>Connected</span>
        ) : (
          <span>{props.label}</span>
        )}
      </label>
      <input id="header_box_r_wallet_button" type="checkbox" />

      {props.isNetwork && (
        <label htmlFor="header_box_r_wallet_button">{networkName}</label>
      )}
      {props.cureentAddress && (
        <div id="header_box_r_wallet_text">
          <span>{props.cureentAddress}</span>
        </div>
      )}
    </div>
  );
}

export default Wallet;
