import { RecommendState } from './store';

export const recommendReducer = (state: RecommendState, action: any) => {
  switch (action.type) {
    case 'SET_TRANCHE_RECOMMEND_BOND_GROUP_LIST':
      return {
        ...state,
        trancheRecommendBondGroupList: action.payload,
      };
    case 'SET_CONVERT_BOND_GROUP':
      return {
        ...state,
        convertLBTBondGroup: action.payload.convertLBTBondGroup,
        convertIDOLBondGroup: action.payload.convertIDOLBondGroup,
      };
    default:
      return state;
  }
};
