import React, { createContext, useCallback, useEffect } from 'react';
import { GlobalState } from 'reducer/store';
import { lien } from 'actions/useLien';
import { ExchangeData } from 'types';
import { useDispatch, useSelector } from 'react-redux';

export const SubscribeContext: any = createContext('');

const ACCEPT_ORDERS_MAP = 'ACCEPT_ORDERS_MAP';
const EXECUTION_MAP = 'EXECUTION_MAP';

export const SubscribeProvider: React.FC = ({ children }): JSX.Element => {
  const dispatch = useDispatch();
  const exchangeDataMap = useSelector(
    (state: GlobalState) => state.base.exchangeDataMap
  );
  const issueIDOLOnly = useSelector(
    (state: GlobalState) => state.txTrigger.issueIDOLOnly
  );
  const issueLBTOnly = useSelector(
    (state: GlobalState) => state.txTrigger.issueLBTOnly
  );
  const newTradeData = useSelector(
    (state: GlobalState) => state.txTrigger.newTradeData
  );
  const executeUnexecutedBox = useSelector(
    (state: GlobalState) => state.txTrigger.executeUnexecutedBox
  );
  const minTrigger = useSelector(
    (state: GlobalState) => state.timeTrigger.minTrigger
  );
  const currentAddress = lien.address;

  const dispatchExchangeEventSubscriber = async (
    exchangeData: ExchangeData,
    filter: any,
    dispatchType: string
  ) => {
    const fromFilter = {
      ...filter,
      fromBlock: 8000000,
    };
    const callFunc = (error: any, events: any[]) => {
      if (error) {
        console.error(error);
      } else if (events.length > 0) {
        dispatch({
          type: `ADD_${dispatchType}`,
          payload: events,
          key: exchangeData.exchangeAddress,
          height: events[events.length - 1].blockNumber + 1,
        });
      }
    };
    switch (dispatchType) {
      case ACCEPT_ORDERS_MAP:
        await lien.subscribe.getExchangeEvents(
          fromFilter,
          exchangeData.exchangeAddress,
          'AcceptOrders',
          callFunc
        );
        break;
      case EXECUTION_MAP:
        await lien.subscribe.getExchangeEvents(
          fromFilter,
          exchangeData.exchangeAddress,
          'Execution',
          callFunc
        );
        break;
    }
  };

  // Triggered new exchangeData
  const dispatchAcceptOrders = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      recipient: currentAddress,
    };
    const dispatchPromiseList = [];
    for (const exchangeData of exchangeDataMap.values()) {
      dispatchPromiseList.push(
        dispatchExchangeEventSubscriber(exchangeData, filter, ACCEPT_ORDERS_MAP)
      );
    }
    await Promise.all(dispatchPromiseList);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    currentAddress,
    exchangeDataMap,
    issueIDOLOnly,
    issueLBTOnly,
    newTradeData,
    executeUnexecutedBox,
    minTrigger,
  ]);

  // Triggered new exchangeData
  const dispatchExecution = useCallback(async () => {
    if (!currentAddress) return;
    const filter = {
      recipient: currentAddress,
    };
    const dispatchPromiseList = [];
    for (const exchangeData of exchangeDataMap.values()) {
      dispatchPromiseList.push(
        dispatchExchangeEventSubscriber(exchangeData, filter, EXECUTION_MAP)
      );
    }
    await Promise.all(dispatchPromiseList);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentAddress, exchangeDataMap, executeUnexecutedBox, minTrigger]);

  useEffect(() => {
    dispatchAcceptOrders();
    dispatchExecution();
    // dispatchTransferEvent();
    // dispatchPendingTransactions();
  }, [
    // dispatchTransferEvent,
    // dispatchPendingTransactions,
    dispatchAcceptOrders,
    dispatchExecution,
  ]);

  return (
    <SubscribeContext.Provider value={{ exchangeDataMap }}>
      {children}
    </SubscribeContext.Provider>
  );
};
