import React from 'react';
import { SIZE } from 'baseui/button';
import { ChevronRight } from 'baseui/icon';
import { Pagination } from 'baseui/pagination';
import {
  Unstable_StatefulDataTable as StatefulDataTable,
  NumericalColumn,
  StringColumn,
  CustomColumn,
  NUMERICAL_FORMATS,
  RowActionT,
} from 'baseui/data-table';
import { Block } from 'baseui/block';
import { AuctionGroupColumn } from '../pages/AuctionListContainer';
import { BigNumber as BN } from 'bignumber.js';
import { StatefulPopover, PLACEMENT } from 'baseui/popover';
import { Label3, Label4, Paragraph4 } from 'baseui/typography';
import { Button } from 'baseui/button';
import NoData from './NoData';
import GettingData from './GettingData';
import useAuction from 'actions/useAuction';
import { ProgressBar } from 'baseui/progress-bar';
import { period } from 'config';
import CopyToClipboard from 'react-copy-to-clipboard';
import { AuctionBid } from 'types';
import { useStyletron } from 'baseui';

//TODO: fix index.d.ts -> import {Unstable_DataTable} from 'baseui/data-table'

interface AuctionListBoxProps {
  rows: { id: any; data: AuctionGroupColumn }[];
  loading?: boolean;
  currentPage: number;
  maxPageNum: number;
  setCurrentPage: (page: number) => void;
  onClickRow:
    | ((event: React.MouseEvent) => void)
    | ((event: { datum?: {}; index?: number }) => void);
  processNextAuctionGroup: (pageNumber: number) => void;
  setLoading: (loading: boolean) => void;
}

interface Bid {
  price: string;
  amount: string;
  random: number;
  reveal: boolean;
}

export default (props: AuctionListBoxProps) => {
  const { setAuctionPage } = useAuction();
  const [css] = useStyletron();
  const columns = [
    CustomColumn({
      title: '#',
      mapDataToValue: (data: AuctionGroupColumn) => data.auctionID,
      renderCell: function Cell(props: any) {
        const auctionIDFull = props.value;
        let auctionID = auctionIDFull;
        if (auctionID.length > 10) {
          auctionID = auctionID.slice(0, 10) + '...';
        }
        return (
          <CopyToClipboard text={auctionIDFull}>
            <StatefulPopover
              content={() => {
                return (
                  <Block padding={'20px'}>
                    copied Auction ID:
                    <p>{`${auctionIDFull}`}</p>
                  </Block>
                );
              }}
              returnFocus
              autoFocus
              animateOutTime={3000}
            >
              <div>{auctionID}</div>
            </StatefulPopover>
          </CopyToClipboard>
        );
      },
      maxWidth: 60,
    }),
    NumericalColumn({
      title: 'Strike Price',
      mapDataToValue: (data: AuctionGroupColumn) => data.strikePrice,
      format: NUMERICAL_FORMATS.ACCOUNTING,
      maxWidth: 60,
    }),
    StringColumn({
      title: 'Maturity',
      mapDataToValue: (data: AuctionGroupColumn) => {
        return data.maturity.toLocaleDateString();
      },
    }),
    CustomColumn({
      title: 'Bid / Amount of sale',
      precision: 2,
      maxWidth: 140,
      mapDataToValue: (data: AuctionGroupColumn) => data,
      renderCell: function Cell(props: any) {
        const floorDecimal2 = (n: number) => Math.floor(n * 100) / 100;
        const auction = props.value as AuctionGroupColumn;
        const saleAmount = new BN(auction.amount).div('1e8');
        const bidAmount = new BN(auction.bidAmount).div('1e8');
        const progress = saleAmount.eq(0)
          ? 0
          : floorDecimal2(bidAmount.times(100).div(saleAmount).toNumber());
        return (
          <div>
            {bidAmount.toString() +
              ' / ' +
              saleAmount.toString() +
              ` (${progress}%)`}
          </div>
        );
      },
    }),
    CustomColumn({
      title: 'Status',
      mapDataToValue: (data: AuctionGroupColumn) => data,
      renderCell: function Cell(props: any) {
        const status = props.value.period;
        const progress = props.value.periodProgress as {
          percent: number;
          label: string;
        };
        return [period.closed].includes(status) ? (
          <div>{status}</div>
        ) : (
          <StatefulPopover
            content={() => (
              <Block padding={'20px'}>
                <Label3>{progress.label}</Label3>
              </Block>
            )}
            returnFocus
            renderAll
            autoFocus
          >
            <div
              className={css({
                width: '100%',
              })}
            >
              {status}
              <ProgressBar value={progress.percent} successValue={100} />
            </div>
          </StatefulPopover>
        );
      },
    }),
    CustomColumn({
      title: 'Bid List',
      mapDataToValue: (data: AuctionGroupColumn) => data.bidList,
      renderCell: function Cell(props: any) {
        const bidList = props.value || [];
        const bidExists = bidList.length > 0;
        const renderBidList = () => {
          return bidList.map((bid: AuctionBid, index: number) => {
            return (
              <div key={index}>
                <Label4>
                  {`Price: $ ${new BN(bid.price).dividedBy('1e8').toString()} `}
                  &emsp;
                  {`Amount: 
                  ${new BN(bid.amount).dividedBy('1e8').toString()} SBT`}
                  &emsp;
                  {bid.reveal ? 'Revealed' : ''}
                </Label4>
              </div>
            );
          });
        };
        return (
          <StatefulPopover
            content={() => (
              <Block padding={'20px'}>
                <Label3>Bid history</Label3>
                <Paragraph4>{bidExists ? renderBidList() : ''}</Paragraph4>
              </Block>
            )}
            placement={PLACEMENT.bottom}
            returnFocus
            renderAll
            autoFocus
          >
            <Button size={SIZE.mini}>
              {bidExists ? 'Attended' : 'No participation'}
            </Button>
          </StatefulPopover>
        );
      },
    }),
  ];

  const rowActions: RowActionT[] = [
    {
      label: 'Action Status',
      onClick: ({ row }) => {
        row.data.index = row.data.id;
        row.data.datum = row.data;
        props.onClickRow(row.data);
      },
      renderIcon: () => <ChevronRight title="" />,
    },
  ];
  return (
    <div>
      <div style={{ height: `${props.rows.length * 60 + 120}px` }}>
        <StatefulDataTable
          columns={columns}
          rows={props.rows}
          rowActions={rowActions}
          rowHeight={60}
          loading={props.loading}
          loadingMessage={() => <GettingData />}
          emptyMessage={() => <NoData />}
          onClick={props.onClickRow}
          filterable={false}
          searchable={false}
        />
      </div>
      <Pagination
        numPages={props.maxPageNum}
        size={SIZE.mini}
        currentPage={props.currentPage}
        onPageChange={({ nextPage }) => {
          props.setLoading(true);
          setAuctionPage(nextPage);
          props.setCurrentPage(Math.min(Math.max(nextPage, 1), 10));
          props.processNextAuctionGroup(nextPage);
        }}
      />
    </div>
  );
};
