import React, { useState, useEffect, useCallback } from 'react';
import { useSelector } from 'react-redux';
import AuctionBidBoard from 'view/components/presentations/AuctionBidBoard';
import { BigNumber as BN } from 'bignumber.js';
import { mocked, auctionBidBoardMock } from 'mock';
import { GlobalState } from 'reducer/store';

type AuctionBidBoardContainerProps = {
  auctionID: string;
  strikePrice: string;
};

function AuctionBidBoardContainer({
  auctionID,
  strikePrice,
}: AuctionBidBoardContainerProps) {
  const [auctionBidBoard, setAuctionBidBoard] = useState<
    { price: number; win: number; lose: number; user: number }[]
  >([]);
  const state = useSelector((state: GlobalState) => state);
  const auctionGroupMap = state.auction.auctionGroupMap;

  const makeBidBoardData = useCallback(() => {
    const auction = auctionGroupMap.get(auctionID);
    if (!auction) {
      return null;
    }
    const result = auction.result;
    const board = auction.board;
    if (!result || !board) {
      return null;
    }

    const sortBidPrice = board.sorted as BN[];
    const boardStatus = board.amounts as BN[];
    const data = sortBidPrice.map((price, priceIndex) => {
      let [win, lose] = [0, 0];
      const endPrice = result.end.price;
      const user = 0;
      const amount = new BN(boardStatus[priceIndex]).toNumber();
      switch (true) {
        case price.gt(endPrice):
          win = amount - user;
          break;
        case price.eq(endPrice):
          win = result.end.auctionEndPriceWinnerSBTAmount.toNumber();
          lose = amount - win;
          break;
        default:
          lose = amount - user;
          break;
      }
      return {
        price: price.toNumber(),
        win,
        lose,
        user,
      };
    });

    if (sortBidPrice.length <= 0) {
      data.push({
        price: parseInt(strikePrice),
        win: 0,
        lose: 0,
        user: 0,
      });
      data.push({
        price: (parseInt(strikePrice) * 9) / 10,
        win: 0,
        lose: 0,
        user: 0,
      });
    }

    return data;
  }, []); // eslint-disable-line

  useEffect(() => {
    const newState = makeBidBoardData();
    if (!newState) {
      return;
    }
    setAuctionBidBoard(newState);
  }, []); // eslint-disable-line

  return (
    <>
      <AuctionBidBoard
        data={mocked ? auctionBidBoardMock : auctionBidBoard}
        height={200}
        width={500}
      />
    </>
  );
}

export default AuctionBidBoardContainer;
