import React, { useState } from 'react';
import ContentTitle from 'view/components/presentations/ContentTitle';
import { Tabs, TabList, Tab, TabPanel } from 'react-tabs';
import { PoolSymbol } from 'types';
import TradeContainer from 'view/components/pages/trade/TradeContainer';
import LiquidityUpperContainer from 'view/components/pages/trade/LiquidityUpperContainer';
import LiquidityAddContainer from 'view/components/pages/trade/LiquidityAddContainer';
import LiquidityRemoveContainer from 'view/components/pages/trade/LiquidityRemoveContainer';
import { withRouter } from 'react-router-dom';
import InfoContainer from 'view/components/pages/InfoContainer';

const tabs = ['/trade', '/trade/liquidity'];
function Trade(props: any) {
  const selectedIndex = tabs.indexOf(props.location.pathname);

  const [selectedSymbol, selectSymbol] = useState<PoolSymbol>();

  return (
    <Tabs
      defaultIndex={selectedIndex !== -1 ? selectedIndex : 0}
      onSelect={(index) => props.history.push(tabs[index])}
    >
      <div id="content_box">
        <ContentTitle title="FairSwap Trade" />
        <TabList>
          <Tab>Trade</Tab>
          <Tab>Provide Liquidity</Tab>
        </TabList>

        <TabPanel>
          <TradeContainer />
          <InfoContainer />
        </TabPanel>
        <TabPanel>
          <LiquidityUpperContainer
            poolSymbol={selectedSymbol}
            onSelectPoolSymbol={selectSymbol}
          />
          <InfoContainer />
          <br />
          <LiquidityAddContainer poolSymbol={selectedSymbol} />
          <LiquidityRemoveContainer poolSymbol={selectedSymbol} />
        </TabPanel>
      </div>
    </Tabs>
  );
}

export default withRouter(Trade);
