import { EventState } from './store';

export const eventReducer = (
  state: EventState,
  action: { type: string; payload: any; key?: any; height?: number }
) => {
  switch (action.type) {
    case 'ADD_PENDING_TRANSACTIONS_DATA':
      return {
        ...state,
        pendingTransactions: action.payload,
      };
    case 'ADD_ACCEPT_ORDERS_MAP': {
      return {
        ...state,
        acceptOrdersListMap: new Map([
          ...(state.acceptOrdersListMap as any),
          ...(new Map([[action.key, [...action.payload]]]) as any),
        ]),
      };
    }
    case 'ADD_EXECUTION_MAP': {
      return {
        ...state,
        executionListMap: new Map([
          ...(state.executionListMap as any),
          ...(new Map([[action.key, [...action.payload]]]) as any),
        ]),
      };
    }
    case 'ADD_TRANSFER_DATA':
      return {
        ...state,
        transferEvent: action.payload,
      };
    default:
      return state;
  }
};
