import React from 'react';
import { ComposedChart, XAxis, YAxis, Bar, Legend } from 'recharts';
import { color } from 'config';

type AuctionBidBoardProps = {
  data: {
    price: number;
    win: number;
    lose: number;
    user: number;
  }[];
  height: number;
  width: number;
};

const defaultProps = {
  height: 300,
  width: 300,
};

function AuctionBidBoard({ data, height, width }: AuctionBidBoardProps) {
  return (
    <div className="auction_bid_board">
      <ComposedChart
        width={width}
        height={height}
        data={data}
        margin={{
          top: 20,
          right: 200,
          bottom: 10,
          left: 0,
        }}
      >
        <XAxis
          dataKey="price"
          label={{
            value: 'iDOL',
            position: 'insideBottomRight',
            offset: 0,
            fontSize: 10,
          }}
          tick={{ fontSize: 10 }}
          padding={{ left: 0, right: 0 }}
        />
        <YAxis
          label={{
            value: 'SBT',
            angle: -90,
            position: 'insideLeft',
            fontSize: 10,
          }}
          tick={{ fontSize: 10 }}
        />
        <Legend />
        <Bar dataKey="user" stackId="a" barSize={8} fill={color.black} />
        <Bar dataKey="lose" stackId="a" barSize={8} fill={color.blue} />
        <Bar dataKey="win" stackId="a" barSize={8} fill={color.red} />
      </ComposedChart>
    </div>
  );
}

AuctionBidBoard.defaultProps = defaultProps;

export default AuctionBidBoard;
