import React from 'react';
import './ItemNumber.scss';
import IconWrapper from 'view/components/presentations/IconWrapper';
import { HelpCircle } from 'react-feather';

interface ItemNumberProps {
  name: string;
  value: string;
  isHelp?: boolean;
  description?: string[];
}

function ItemNumber(props: ItemNumberProps) {
  return (
    <div className="number">
      {props.isHelp ? (
        <IconWrapper
          component={<span>{props.name}</span>}
          icon={<HelpCircle size={14} color="#666" />}
          isDescription={true}
          description={props.description || []}
          isPadded={false}
        />
      ) : (
        <span>{props.name}</span>
      )}
      <span>{props.value}</span>
    </div>
  );
}

export default ItemNumber;
