import React, { createContext, useEffect, useCallback, useState } from 'react';
import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  ROLE,
  SIZE,
} from 'baseui/modal';
import Wallet from 'view/components/presentations/HeaderWallet';
import useLien, { lien } from 'actions/useLien';
import { Button } from 'baseui/button';
import { useDispatch } from 'react-redux';

export const OnLoadProciderContext: any = createContext('');

export const OnLoadProvider: React.FC = ({ children }): JSX.Element => {
  const dispatch = useDispatch();
  const { connect } = useLien();
  const [isOpen, setIsOpen] = React.useState(true);
  const [selectedAddress, setSelectedAddress] = React.useState('');
  const [isError, setIsError] = useState<boolean>(false);
  const currentAddress = lien.address;

  const connectFunc: () => void = async () => {
    const res = await connect();
    if (res) return setIsError(true);
    setIsOpen(false);
  };

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const accountsChanged = useCallback(async () => {
    if (!currentAddress) return;
    const ethereum = await lien.getProvider.getEthereumProviderApi();
    ethereum.on('accountsChanged', (accounts: string[]) => {
      setSelectedAddress(accounts[0]);
    });
    if (selectedAddress !== currentAddress) {
      dispatch({
        type: 'CLEAR_BASE_MY',
      });
      lien.resetAccount();
      setIsOpen(true);
    }
  }, [selectedAddress]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    accountsChanged();
  }, [accountsChanged]);

  return (
    <OnLoadProciderContext.Provider value={{ isOpen }}>
      <Modal
        onClose={() => setIsOpen(false)}
        isOpen={isOpen}
        closeable={false}
        animate
        autoFocus
        size={SIZE.default}
        role={ROLE.dialog}
      >
        <ModalHeader>Sign in</ModalHeader>
        <ModalBody>Please connect a wallet to Ropsten network</ModalBody>
        {isError ? (
          <ModalFooter>
            <Button disabled>No ethereum Wallet</Button>
          </ModalFooter>
        ) : (
          <ModalFooter>
            <Wallet connect={connectFunc} label={'MetaMask'} />
          </ModalFooter>
        )}
      </Modal>
      {children}
    </OnLoadProciderContext.Provider>
  );
};
